import { WllamaChatMessage } from '@wllama/wllama';


export function ChatMessage({ role, content }: WllamaChatMessage) {

    const messageStyle = {
        padding: '10px',
        borderRadius: '8px',
        maxWidth: '75%',
        display: 'inline-block',
        marginBottom: '10px',
        wordWrap: 'break-word',
        fontSize: '14px',
    } as const

    const metaStyle = {
        fontSize: '0.8rem',
        color: '#888',
        marginTop: '5px',
    } as const

    const senderStyle = {
        fontWeight: 'bold',
    } as const

    const containerStyle = {
        display: 'flex',
        flexDirection: 'column',
        alignItems: role === 'user' ? 'flex-end' : 'flex-start',
    } as const

    const sentStyle = {
        ...messageStyle,
        backgroundColor: '#dcf8c6',
    } as const

    const receivedStyle = {
        ...messageStyle,
        backgroundColor: '#e07980',
    } as const

    return <>
        <div style={containerStyle}>
            <div style={role === 'user' ? sentStyle : receivedStyle}>
                {content}
            </div>
            <div style={metaStyle}>
                <span style={senderStyle}>{role}</span>
            </div>
        </div>
    </>
}
