import '@react-sigma/core/lib/style.css';
import { FC } from 'react';
import { CorpusManager } from './CorpusManagerPane';
import { MainMenu } from './MainMenu';
import { ModelManagerPane } from './ModelManagerPane';
import { QueryPanel } from '@/components/QueryWorkbench';
import { useWebRag } from '@/useWebRAG';


export const App: FC<{}> = ({ }) => {

	const currentPage = useWebRag(state => state.currentPage)

	return <>
		{currentPage === 'MainMenu' ? <MainMenu /> : ''}
		{currentPage === 'CorpusManager' ? <CorpusManager /> : ''}
		{currentPage === 'ModelManager' ? <ModelManagerPane /> : ''}
		{currentPage === 'QueryPanel' ? <QueryPanel /> : ''}
	</>
}

