import '@react-sigma/core/lib/style.css';
import { SigmaContainer, useCamera, useRegisterEvents } from '@react-sigma/core';
import { LayoutForceAtlas2Control } from '@react-sigma/layout-forceatlas2';
import { CSSProperties, FC, useEffect, useState } from 'react';
import { Corpus } from '../Corpus';
import { corpusToGraph } from '../corpusToGraph';
import { useWebRag } from '@/useWebRAG';



export const GraphView: FC<{}> = ({ }) => {

	const corpus = useWebRag(state => state.corpus)
	const docIdsUnderFocus = useWebRag(state => state.docIdsUnderFocus)

	const sigmaContainerStyle: CSSProperties = {
		border: 'solid black',
		height: '500px',
		width: '500px',
	}

	


	return <SigmaContainer
		style={sigmaContainerStyle}
		graph={corpusToGraph(corpus, docIdsUnderFocus)}
	>

		{/* <UseCamera /> */}

		<DocDetailView corpus={corpus} />

		<LayoutForceAtlas2Control
			settings={{
				settings: {
					gravity: 10,
					barnesHutOptimize: true,
					scalingRatio: 100,
					slowDown: 1000
				}
			}} />

	</SigmaContainer>

}


