import { HTML } from "@/HTML"
import { Doc } from "./Doc"
import { JsonCache } from "./JsonCache"
import { deduplicate } from "./deduplicate"


export class Corpus {

    readonly docs: Doc[]
    readonly idToDoc: { [key: string]: Doc }

    constructor(docs: Doc[]) {
        this.docs = deduplicate(docs, x => x.id).map(x => x.setCorpus(this))
        this.idToDoc = Object.fromEntries(this.docs.map(x => [x.id, x]))
    }

    async embedded({ embed, progressCallback }: {
        embed: (text: string) => Promise<number[]>,
        progressCallback: ({ done, total }: { done: number, total: number }) => void,
    }) {
        const total = this.docs.length
        const embeddedDocs: Doc[] = []

        for (let i = 0; i < this.docs.length; i++) {

            const embeddedDoc = await this.docs[i].embed(embed)
            embeddedDocs.push(embeddedDoc)
            progressCallback({ done: i, total })
        }

        return new Corpus(embeddedDocs)
    }

    async find(
        embed: (text: string) => Promise<number[]>,
        query: number[] | string,
        minSim: number,
        limit: number,
        prefilter: (doc: Doc) => boolean = x => true,
    ): Promise<Doc[]> {

        const queryEmbedding = typeof query === 'string' ? await embed(query) : query
        // TODO!

        let docsAndScores = this.docs.filter(prefilter).map(x => [x, cosine(x.embedding, queryEmbedding)] as const)
        docsAndScores = docsAndScores.filter(x => x[1] >= minSim)
        docsAndScores.sort((a, b) => b[1] - a[1]) // TODO: asc desc????!
        docsAndScores = docsAndScores.slice(0, limit)
        const topDocs = docsAndScores.map(x => x[0])
        return topDocs
    }

    toList() {
        return this.docs.slice(0)
    }

    has(id: string) {
        return id in this.idToDoc
    }

    parseMetadata(parseMetadata: (doc: Doc) => { [key: string]: unknown }) {
        return this.toList().map(x => x.copy({ metadata: parseMetadata(x) }))
    }

    get(id: string): Doc | undefined {
        return this.idToDoc[id]
    }

    // TODO!
    parseRefs() { throw new Error() }
    reifyRefs() { throw new Error() }
    roots() { throw new Error() }
    partsOf() { throw new Error() }
    reduce() { throw new Error() }

    static fromFiles = async (files: File[]): Promise<Corpus> => {
        const corpora = await Promise.all(files.map(x => Corpus.fromFile(x)))
        return Corpus.join(corpora)
    }

    static fromFile = async (file: File) => {

        const ext = file.name.split('.').at(-1)!
        const selector: { [ext: string]: (file: File) => Promise<Corpus> } = {
            'json': this.fromJsonFile,
            'html': this.fromHtmlFile,
            // TODO: from pdf
        }
        const parse = selector[ext]
        const corpus = parse(file)
        return corpus
    }

    static fromJsonFile = async (file: File) => {

        const text = await file.text()
        const json = JSON.parse(text)
        const corpus = Corpus.fromJson(json)
        return corpus
    }

    static fromHtmlFile = async (file: File): Promise<Corpus> => {
        return new HTML(await file.text(), file.name).toCorpus()
    }

    static fromJson = (docs: ConstructorParameters<typeof Doc>[0][]) => {
        return new Corpus(docs.map(Doc.fromJson))
    }

    static join = (corpora: Corpus[]) => {
        return new Corpus(corpora.flatMap(x => x.toList()))
    }

    size() {
        return this.docs.length
    }

    static fromDisk() {
        const cache = new JsonCache('corpus')
        const corpusJson = cache.get('globalCorpus') ?? []
        return Corpus.fromJson(corpusJson)
    }

    // with or without embeddings??
    toDisk() {
        const cache = new JsonCache('corpus')
        cache.set('globalCorpus', this.docs.map(x => x.toJson()))
    }

    percentageEmbedded() {
        return this.docs.map(x => x.hasEmbedding()).reduce((a, b) => a + (b ? 1 : 0), 0) / this.docs.length
    }

}

function norm(embedding: number[]) {
    return embedding.reduce((a, b) => a + b ** 2, 0) ** 0.5
}

function normalize(embedding: number[]) {
    return embedding.map(x => x / norm(embedding))
}

function cosine(embedding1: number[], embedding2: number[]) {
    const n1 = normalize(embedding1)
    const n2 = normalize(embedding2)
    return n1.map((v1, i) => v1 * n2[i]).reduce((a, b) => a + b, 0)
}
