import { useWebRag } from "@/useWebRAG"


export function MainMenu() {

    const isGenModelLoaded = useWebRag(state => state.isGenModelLoaded)
    const isEmbedModelLoaded = useWebRag(state => state.isEmbedModelLoaded)
    const corpus = useWebRag(state => state.corpus)
    const setCurrentPage = useWebRag(state => state.setCurrentPage)

    const modelsLoaded = isGenModelLoaded && isEmbedModelLoaded
    const corpusEmpty = corpus.size() === 0

    return <>
        <div>
            <center>
                <h1 style={{ margin: '100px' }}>Web RAG</h1>
            </center>

            <center>
                {!modelsLoaded ? <div>
                    <span style={{ color: 'red' }}>Models not loaded.</span> Go to model manager.
                </div> : <></>}
            </center>

            <center>
                {modelsLoaded && corpusEmpty ? <div>
                    <span style={{ color: 'red' }}>Corpus is empty.</span> Go to corpus manager.
                </div> : <></>}
            </center>

            <center>
                {modelsLoaded && !corpusEmpty ? <div>
                    <span style={{ color: 'green' }}>Models ok, corpus detected.</span> Try out the query workbench.
                </div> : <></>}
            </center>

            <table style={{ margin: '10px auto', fontSize: '20px' }}>

                <tbody>
                    <tr>
                        <td><button onClick={() => setCurrentPage('ModelManager')} >Model Manager</button></td>
                        <td><button disabled={!modelsLoaded} onClick={() => setCurrentPage('CorpusManager')} >Corpus Manager</button></td>
                        <td><button disabled={!modelsLoaded || corpusEmpty} onClick={() => setCurrentPage('QueryPanel')} >Query Workbench</button></td>
                    </tr>
                </tbody>

            </table>

        </div>
    </>
}



