import { create } from 'zustand'
import { Corpus } from './Corpus';
import { Wllama, WllamaChatMessage } from '@wllama/wllama';
import { JsonCache } from './JsonCache';

type CurrentPage = 'MainMenu' | 'CorpusManager' | 'ModelManager' | 'QueryPanel'

interface AppState {
    corpus: Corpus,
    messages: WllamaChatMessage[],
    isGenModelLoaded: boolean,
    isEmbedModelLoaded: boolean,
    downloadProgress: number,
    embeddingProgress: number,
    currentPage: CurrentPage,
    embedder: Wllama,
    generator: Wllama,
    docIdsUnderFocus: string[],


    setCurrentPage: (page: CurrentPage) => void,
    processUserMessage: (content: string) => void,
    downloadModel: (url: string, isEmebedding: boolean) => Promise<void>,
    sideloadModel: (files: File[]) => Promise<void>,
    updateCorpus: (...corpora: Corpus[]) => void,
    embedCorpus: () => Promise<void>,
}

const pathConfig = {
    'single-thread/wllama.wasm': './single-thread-wllama.wasm',
    'multi-thread/wllama.wasm': './multi-thread-wllama.wasm',
}

const embed = async (text: string, embedder: Wllama): Promise<number[]> => {

    const cache = new JsonCache('llmcache')

    const cached = cache.get(text)

    if (cached) {
        return cached
    }

    const value = await embedder.createEmbedding(text)
    cache.set(text, value)
    return value
}

// const systemPrompt: WllamaChatMessage = {
//     role: 'system',
//     content: 'You are a helpful assistant that answers user questions based on retrived chunks of data.'
// }

export const useWebRag = create<AppState>((set, get) => ({

    // TODO!!!!!!!!
    currentPage: 'MainMenu',
    // currentPage: 'CorpusManager',


    docIdsUnderFocus: [],

    corpus: Corpus.fromDisk(),
    // messages: [systemPrompt],
    messages: [],
    isGenModelLoaded: false,
    isEmbedModelLoaded: false,
    downloadProgress: 0,
    embeddingProgress: 0,
    embedder: new Wllama(pathConfig),
    generator: new Wllama(pathConfig),


    setCurrentPage: (currentPage) => set({ currentPage }),

    updateCorpus: async (...corpora: Corpus[]) => {

        const { corpus, embedCorpus } = get()
        const joinedCorpus = Corpus.join([corpus, ...corpora])
        const newCorpus = joinedCorpus
        newCorpus.toDisk()
        set({ corpus: newCorpus })
        await embedCorpus()
    },

    embedCorpus: async () => {

        // TODO: check and warn if embedder not loaded!
        const { corpus, embedder } = get()
        const newCorpus = await corpus.embedded({
            embed: text => embed(text, embedder),
            progressCallback: ({ done, total }) => set({ embeddingProgress: 100 * done / total })
        })
        set({ corpus: newCorpus })
        newCorpus.toDisk()
    },

    processUserMessage: async (content: string) => {

        const { messages, generator, corpus, embedder } = get()

        const retrievedDocs = await corpus.find(
            text => embed(text, embedder), 
            content, 
            0.2,
            1,
            // x => x.body.length <= 300
        )
        
        const retrievedDocIds = retrievedDocs.map(x=>x.id)

        set({docIdsUnderFocus: retrievedDocIds})

        const retrievedContext = retrievedDocs.map(x => `${x.title}: ${x.body}\n`).join('')

        console.log('retrievedContext=', retrievedContext)

        // The LLM sees this, but the user sees their original message
        const shadowUserMessage: WllamaChatMessage = {
            role: 'user',
            content: `Answer the following question using ONLY information from the following context.\n\nThe question:\n${content}\n\nThe context:\n${retrievedContext}\n\nYour answer:\n`
        }

        console.log('shadowUserMessage=', shadowUserMessage.content)

        // Q: What happens in case a method execution fails?

        const newMessages: WllamaChatMessage[] = [
            ...messages,
            { role: 'user', content },
        ]

        set({ messages: newMessages })
        
        // TMP turning off chat!
        // const shadowMessages = [...messages, shadowUserMessage]
        const shadowMessages = [shadowUserMessage]

        const stream = await generator.createChatCompletion(shadowMessages, {
            stream: true,
            useCache: true,
        })

        // TODO: "fetching/processing" progress bar

        for await (const { currentText } of stream) {
            set({ messages: [...newMessages, { role: 'assistant', content: currentText }] })
        }
    },

    downloadModel: async (url: string, isEmbedding: boolean) => {

        const m = new Wllama(pathConfig)

        await m.loadModelFromUrl(
            url, {
            useCache: true,
            embeddings: isEmbedding,
            progressCallback: ({ loaded, total }) => set({ downloadProgress: 100 * loaded / total })
        })

        // set model as current one ... keep?
        if (isEmbedding) {
            set({ embedder: m, isEmbedModelLoaded: true })
        } else {
            set({ generator: m, isGenModelLoaded: true })
        }

    },
    sideloadModel: async (files: File[]) => {

        // TODO: don't assume, require isEmbedding param
        const embedFiles = files.filter(x => x.name.toLowerCase().includes('embed'))
        const genFiles = files.filter(x => !embedFiles.includes(x))

        if (embedFiles.length > 0) {
            const e = new Wllama(pathConfig)
            await e.loadModel(embedFiles, { embeddings: true })
            set({ embedder: e, isEmbedModelLoaded: true })
            // set({ embedder: e })
        }

        if (genFiles.length > 0) {
            const g = new Wllama(pathConfig)
            // TODO: set state: loading model
            await g.loadModel(genFiles)
            set({ generator: g, isGenModelLoaded: true })
            // set({ generator: g })
        }
    }
}))




