import { ChangeEvent } from "react"
import { Corpus } from "../Corpus"
import { useWebRag } from "@/useWebRAG"
import { GraphView } from "./GraphCanvas"

// TODO: delete, download...

export function CorpusManager() {

    const setCurrentPage = useWebRag(state => state.setCurrentPage)

    return <div>

        <button onClick={() => setCurrentPage('MainMenu')}>Back to home</button>

        <h2>Corpus Manager</h2>

        <p>In the corpus manager you can inspect the current status of the corpus and add new documents to it.</p>

        <table>
            <tbody>
                <tr>
                    <td>
                        <LoadCorpusPane />
                    </td>
                    <td>
                        <GraphView />
                    </td>
                </tr>
            </tbody>
        </table>

    </div>
}

export function LoadCorpusPane() {

    const updateCorpus = useWebRag(state => state.updateCorpus)
    const embeddingProgress = useWebRag(state => state.embeddingProgress)
    const embedCorpus = useWebRag(state => state.embedCorpus)

    const handle = async (event: ChangeEvent<HTMLInputElement>) => {
        const { files } = event.target
        if (files === null) throw new Error()
        const blobs = Array.from(files)
        const deltaCorpus = await Corpus.fromFiles(blobs)
        updateCorpus(deltaCorpus)
    }

    return <div>

        <h3>Add a document</h3>

        <p>As of now, the current file formats are supported: .html.</p>

        <input type='file' accept='.json, .html' multiple onChange={handle} />


        <h3>Embed corpus</h3>

        <p>Manually trigger the computation of vector embeddings for the corpus documents.</p>

        <button onClick={embedCorpus}>Compute Embeddings</button>

        <p>{embeddingProgress}</p>

        <progress max={100} value={embeddingProgress} />
    </div>
}