import { Corpus } from "./Corpus"

export class Doc {

    constructor(readonly data: {
        id: string,
        title: string,
        body?: string,
        partIds?: string[],
        refIds?: string[],
        metadata?: { [key: string]: unknown },
        embedding?: number[],
        corpus?: Corpus,
    },
        readonly id = data.id,
        readonly title = data.title,
        readonly body = data.body ?? '',
        readonly partIds = data.partIds ?? [],
        readonly refIds = data.refIds ?? [],
        readonly metadata = data.metadata ?? {},
        readonly embedding = data.embedding,
        readonly corpus = data.corpus,
    ) {

    }

    copy(kwargs: Partial<ConstructorParameters<typeof Doc>[0]> = {}) {

        return new Doc({
            ...this.data,
            ...kwargs
        })
    }

    setCorpus(corpus: Corpus) {
        return this.copy({ corpus })
    }

    async embed(embed: (text: string) => Promise<number[]>) {
        return this.copy({ embedding: await embed(this.body) })
        // return await model.embed(this.body)
    }

    attributes() {

        const { metadata, ...rest } = this.data

        return {
            ...rest,
            ...metadata,
            label: this.data.title,
        }
    }

    edges() {

        return [
            ...this.refIds.map(x => ({ from: this.id, to: x, label: 'references' })),
            ...this.partIds.map(x => ({ from: this.id, to: x, label: 'contains' })),
        ]
    }

    static fromJson(data: ConstructorParameters<typeof Doc>[0]) {
        return new Doc(data)
    }

    toJson() {
        const { corpus, ...data } = this.data
        return data
    }

    hasEmbedding(){
        return !!this.embedding
    }

}
