
export function levenshtein(s1: string, s2: string): number {
    if (s1.length > s2.length) {
        [s1, s2] = [s2, s1]
    }

    let distances: number[] = []

    for (let i = 0; i < s1.length + 1; i++) {
        distances.push(i)
    }

    s2.split('').forEach((c2, i2) => {
        const distances2 = [i2 + 1]

        s1.split('').forEach((c1, i1) => {

            if (c1 === c2) {
                distances2.push(distances[i1])
            } else {
                distances2.push(1 + Math.min(distances[i1], distances[i1 + 1], distances2.at(-1)!))
            }

        })
        distances = distances2
    })

    return distances.at(-1)!
}