export function deduplicate<T>(
    elements: T[],
    key: (x: T) => string,
    keepLast: boolean = true,
): T[] {

    const keys = elements.map(key)

    return elements.filter((_, i) => {

        if (keepLast) {
            return !keys.slice(i + 1).includes(keys[i])
        } else {
            return !keys.slice(0, i).includes(keys[i])
        }

    })
}

