import { Corpus } from "./Corpus";
import { MultiDirectedGraph } from 'graphology';

export function corpusToGraph(corpus: Corpus, docIdsUnderFocs: string[]) {

	const edges = corpus.toList().flatMap(x => x.edges())
	const attributes = corpus.toList().map(x => x.attributes())
	const graph = new MultiDirectedGraph();

	for (const node of attributes) {
		graph.addNode(node.id, {
			x: 100 * Math.random(),
			y: 100 * Math.random(),
			size: 10,
			color: docIdsUnderFocs.includes(node.id)? 'red' : 'grey',
			...node,
		})
	}

	for (const edge of edges) {
		graph.addEdge(edge.from, edge.to, edge)
	}

	return graph
}