import { ChatBody } from "./ChatBody";
import { ChatInput } from "./ChatInput";
import { useWebRag } from "@/useWebRAG";


export function ChatPane({ }: {}) {

    const messages = useWebRag(state => state.messages)
    const processUserMessage = useWebRag(state => state.processUserMessage)

    return <>
        <ChatBody messages={messages} />
        <ChatInput onUserMessage={processUserMessage} />
    </>

}