import { useWebRag } from "@/useWebRAG";
import { useState } from "react"
import { ChangeEvent } from "react"




export function ModelManagerPane() {

    const setCurrentPage = useWebRag(state => state.setCurrentPage)
    const isEmbedModelLoaded = useWebRag(state => state.isEmbedModelLoaded)
    const isGenModelLoaded = useWebRag(state => state.isGenModelLoaded)
    const downloadModel = useWebRag(state => state.downloadModel)
    const downloadProgress = useWebRag(state => state.downloadProgress)
    const loadFromFiles = useWebRag(state => state.sideloadModel)

    return <div style={{ margin: '100px' }}>

        <button onClick={() => setCurrentPage('MainMenu')}>Back to home</button>

        <h2>Model Manager</h2>

        <p>The model manager helps you download required models files.</p>

        <IsModelLoadedPane isEmbedModelLoaded={isEmbedModelLoaded} isGenModelLoaded={isGenModelLoaded} />

        <DownloadModelPane downloadModel={downloadModel} downloadProgress={downloadProgress} />

        <SideloadModelPane loadFromFiles={loadFromFiles} />

    </div>

}

export function IsModelLoadedPane({
    isGenModelLoaded,
    isEmbedModelLoaded
}: {
    isGenModelLoaded: boolean,
    isEmbedModelLoaded: boolean
}) {

    const notLoaded = <span style={{ color: 'red' }}>NOT LOADED :-(</span>
    const loaded = <span style={{ color: 'green' }}>LOADED  :-)</span>

    return <div>
        <div>Generator model {isGenModelLoaded ? loaded : notLoaded}</div>
        <div>Embedding model {isEmbedModelLoaded ? loaded : notLoaded}</div>
    </div>
}


export function DownloadModelPane({ downloadModel, downloadProgress }: {
    downloadModel: (url: string, isEmbedding: boolean) => Promise<void>,
    downloadProgress: number,
}) {

    <h3>Download Model</h3>

    const embeddingModels = [
        'https://huggingface.co/ChristianAzinn/gte-small-gguf/resolve/main/gte-small.Q2_K.gguf'
    ]

    const generationModels = [
        'https://huggingface.co/unsloth/gemma-3-270m-it-GGUF/resolve/main/gemma-3-270m-it-Q3_K_M.gguf'
    ]

    const [selectedUrls, setSelectedUrls] = useState({
        embed: embeddingModels[0],
        gen: generationModels[0],
    })

    const [isDownloading, setDownloading] = useState(false)

    const setEmbedModelUrl = (event: ChangeEvent<HTMLSelectElement>) => {
        setSelectedUrls({ ...selectedUrls, embed: event.target.value })
    }

    const setGenerateModelUrl = (event: ChangeEvent<HTMLSelectElement>) => {
        setSelectedUrls({ ...selectedUrls, gen: event.target.value })
    }

    const startDownload = async () => {
        setDownloading(true)
        await downloadModel(selectedUrls.embed, true)
        await downloadModel(selectedUrls.gen, false)
        setDownloading(false)
    }

    return <div>

        <h3>Download Models</h3>

        <p>Once downloaded, the models will be cached for some time; so if you hit "Download Selected Models" it should be faster the second time.</p>

        <h4>Choose an embedding model</h4>

        <select value={selectedUrls.embed} onChange={setEmbedModelUrl}>
            {embeddingModels.map((x, i) => <option key={i}>{x}</option>)}
        </select>

        <h4>Choose a generator model</h4>

        <select value={selectedUrls.gen} onChange={setGenerateModelUrl}>
            {generationModels.map((x, i) => <option key={i}>{x}</option>)}
        </select>

        <br />
        <br />
        <button onClick={startDownload}>Download Selected Models</button>

        <br />
        <br />

        {isDownloading ? <progress value={downloadProgress} max={100}></progress> : <></>}

    </div >

    // const [selectedUrl, setSelectedUrl] = useState(Object.keys(isEmbedding)[0])

    // return <div>
    //     <select
    //         value={selectedUrl}
    //         onChange={e => setSelectedUrl(e.target.value)}>
    //         {Object.keys(isEmbedding).map((x, i) => <option key={i}>{x}</option>)}
    //     </select>
    //     <button onClick={() => { downloadModel(selectedUrl, isEmbedding[selectedUrl]) }}>Download</button>
    //     <br />
    //     <progress value={downloadProgress} max={100}></progress>
    // </div>
}


export function SideloadModelPane({ loadFromFiles }: {
    loadFromFiles: (files: File[]) => Promise<void>,
}) {

    const handle = async (event: ChangeEvent<HTMLInputElement>) => {

        const { files } = event.target
        if (files === null) throw new Error()
        const blobs = Array.from(files)
        await loadFromFiles(blobs)
    }

    return <div>

        <h3>Sideload Models</h3>

        <p>
            Alternatively, if you already have the GGUF files on you disk, you can load them from here.
        </p>

        <input type='file' accept='.gguf' multiple onChange={handle} />
    </div>
}