import { Corpus } from "@/Corpus";
import { Doc } from "@/Doc";
import { levenshtein } from "@/levensthein";



/**
 * Parses an html file to a Corpus of information units.
 */
export class HTML {

    readonly topElement: HTMLElement

    constructor(html: string, title: string) {

        const element = document.createElement('html');
        element.innerHTML = html
        this.topElement = this.unwrap(element, title)
    }

    unwrap(element: HTMLElement, title: string): HTMLElement {
        // Remove top-level useless tags if present.

        const body = element.querySelector('body')

        if (body === null) {
            return element
        }

        const content = body.firstChild

        if (!(content instanceof HTMLElement)) throw new Error()

        content.id = title
        return content
    }

    toCorpus(): Corpus {
        return new Corpus(this.fixHrefs(this.parseTop(this.topElement), 0.85))
    }

    isBiblio(element: Element): boolean {
        return element.id.includes('biblio')
    }

    parseBiblio(element: Element): Doc[] {

        // The bibliography, if present, contains references to be reified as documents.

        // assume the bibliography has anchors ...
        const anchors = Array.from(element.querySelectorAll('a'))
        // ...anchors identify their grandparent
        const docs = anchors.map(a => new Doc({ id: a.id, body: '', title: a.parentElement?.parentElement?.textContent! }))
        return docs
    }

    parseElement(element: Element): Doc[] {

        const id = element.id
        if (!id) throw new Error()

        // TODO: mark tables!!!

        const children = Array.from(element.children)
        const divsWithId = children.filter(x => x.tagName === 'DIV' && x.id)
        const headings = children.filter(x => x.tagName.startsWith('H'))
        const other = children.filter(x => ![...headings, ...divsWithId].includes(x))
        const anchors = other.filter(x => x.tagName === 'a')
        const body = other.map(x => x.textContent).reduce((a, b) => a + (b ?? '') + '\n\n', '')!
        const hrefs = anchors.map(x => x.getAttribute('href')!).filter(x => x)
        const subdocs = divsWithId.map(x => this.parseTop(x)).flat()
        const partIds = divsWithId.map(x => x.id).filter(x => subdocs.map(sd => sd.id).includes(x))
        const title = headings.map(x => x.textContent ?? '').join(' ').trim() ?? id
        const doc = new Doc({ id, title, body, partIds, refIds: hrefs })
        return [doc, ...subdocs]
    }

    parseTop(element: Element): Doc[] {

        if (this.isBiblio(element)) {
            return this.parseBiblio(element)
        } else {
            return this.parseElement(element)
        }
    }

    fixHrefs(docs: Doc[], minSim: number) {

        // Sometimes the hrefs don't perfectly match the IDs of the tags
        // they refer to; because they're fragments (i.e. #), or because they're using 
        // a non-standard naming convention.

        const ids = docs.map(({ id }) => id)
        const refids = docs.flatMap(({ refIds }) => refIds)
        const subst: { [key: string]: string } = {}

        for (const refid of refids) {
            for (const id of ids) {

                if (levenshtein(id, refid) >= minSim) {
                    subst[refid] = id
                    break
                }
            }
        }

        return docs.map(x => x.copy({ refIds: x.refIds.map(id => subst[id] ?? id) }))
    }

}
